import {
	mapState
} from "vuex"
export default {
	data() {
		return {}
	},
	computed: {
		...mapState(["musicList"])
	},
	methods: {
		input(e) {
			console.log(e.detail.value)
			let ssWords = e.detail.value
			this.$store.dispatch("getMusicList", ssWords)
		},
		playSong(item) {
			let {
				name,
				id
			} = item
			uni.navigateTo({
				url: "../play/play?id=" + id + "&name=" + name
			})
		},
	}
}
